@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@200;300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow:hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.openModalButton{
  font-family:'Noto Sans JP',sans-serif;
  font-size:15px;
  letter-spacing:1px;
  display:inline-block;
  margin-top:5vh;
  padding:2vh 3vw;
  border-radius:40px;
  background:linear-gradient(90deg,#87ceeb,#afeeee);
  box-shadow: 5px 5px 5px #00000040;
  cursor:pointer;
  transition:all .2s;
}

.openModalButton:hover{
  -webkit-transform:translateY(-2px);
          transform:translateY(-2px);
  box-shadow: 10px 10px 10px #00000035;
}

.openModalButton:active{
  box-shadow: 5px 5px 5px #00000060;
  -webkit-transform:translateY(.5px);
          transform:translateY(.5px);
}


.overray{
  position:absolute;
  width:100vw;
  height:100vh;
  background-color:#000;
  opacity:0;
  z-index:-1;
  transition:opacity 1s;
}

.overrayActive{
  opacity:0.66;
  z-index:1;
}

.closeButton{
  position:absolute;
  top:10px;
  right:20px;
  font-size:7vh;
  color:#fff;
  cursor:pointer;
  opacity:0;
  z-index:-1;
}

.closeButtonActive{
  opacity:1;
  z-index:3;
}

.pref-heading{
  font-family:'Noto Sans JP',sans-serif;
}
.Header{
    background-color:#000;
    padding:15px 0;
    text-align:center;
}

.HeaderTitle{
    color:#fff;
    font-family: 'Noto Sans JP',sans-serif;
    font-size:23px;
    font-weight:bold;
    letter-spacing:1px;
}
.CheckBoxs{
    margin-top:20px;
    display:grid;
    grid-template-columns:repeat(8,1fr);
    grid-row-gap:10px;
}

.checkbox-label{
    font-family:'Noto Sans JP',sans-serif;
    font-weight:200;
    cursor:pointer;
}

.checkbox{
    cursor:pointer;
}


@media screen and (max-height:650px){
    .CheckBoxs{
        width:70vw;
        height:70vh;
        padding:50px;
        position:absolute;
        top:-150%;
        left:50%;
        -webkit-transform:translate(-50%,-50%);
                transform:translate(-50%,-50%);
        background-color:#fff;
        z-index:1;
        opacity:0;
        font-size:14px;
        grid-template-columns:repeat(5,1fr);
        transition:top .8s,opacity .5s;
    }

    .checkbox{
        font-size:14px;
    }
}

@media screen and (max-width:760px){
    .CheckBoxs{
        width:70vw;
        height:70vh;
        padding:50px;
        position:absolute;
        top:-150%;
        left:50%;
        -webkit-transform:translate(-50%,-50%);
                transform:translate(-50%,-50%);
        background-color:#fff;
        z-index:1;
        opacity:0;
        font-size:14px;
        grid-template-columns:repeat(4,1fr);
        transition:top .8s,opacity .5s;
    }
}

@media screen and (max-width:450px){
    .CheckBoxs{
        padding:10px;
        width:85vw;
        height:65vh;
        font-size:10px;
    }
}

@media screen and (max-height:500px){
    .CheckBoxs{
        padding:10px;
        height:80vh;
        width:90vw;
        grid-template-columns:repeat(6,1fr);
        font-size:8px;
    }
}



.modalActive{
    top:50%;
    opacity:1;
}
.graph-container{
    height:40vh;
    width:90vw;
    position:relative;
    padding-left:20px;
    padding-right:0;
}

.graph-heading{
    font-family:'Noto Sans JP',sans-serif;
}

.y-label{
    font-family:'Noto Sans JP',sans-serif;
    font-size:16px;
    letter-spacing:1px;
    font-weight:bold;

    position:absolute;
    top:-5px;
    left: 22px;
}

.x-label{
    font-family:'Noto Sans JP',sans-serif;
    font-weight:bold;
    font-size:16px;
    letter-spacing:1px;
    position:absolute;
    bottom:0;
    right:-20px;
}

@media screen and (max-width:600px){
    .graph-container{
        width:85vw;
    }
}

@media screen and (max-height:650px){
    .graph-container{
        height:55vh;
    }
}

@media screen and (max-height:460px){
    .graph-heading{
        display:none;
    }

    .graph-container{
        margin-top:20px;
    }
}

@media screen and (max-width:760px){
    .graph-container{
        height:55vh;
    }
}
